@import "../../scss/variables";

.wp-block-gs-staff-list {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;

  .flip-card {
	display: block;
	background-color: transparent;
	width: 250px;
	height: 250px;
	border: 1px solid #f1f1f1;
	perspective: 1000px;
	margin: 5px;

	.flip-card-inner {
	  position: relative;
	  width: 100%;
	  height: 100%;
	  text-align: center;
	  transition: transform 0.8s;
	  transform-style: preserve-3d;

	  .flip-card-front, .flip-card-back {
		position: absolute;
		width: 100%;
		height: 100%;
		-webkit-backface-visibility: hidden; /* Safari */
		backface-visibility: hidden;
	  }

	  .flip-card-front {
		background-color: #bbb;
		color: black;

		img {
		  object-fit: cover;
		  width: 100%;
		  height: 100%;
		}
	  }

	  .flip-card-back {
		background-color: dodgerblue;
		color: white;
		transform: rotateY(180deg);
	  }
	}

	// Do an horizontal flip on hover
	&:hover .flip-card-inner {
	  transform: rotateY(180deg);
	}

	.name {
	  font-weight: bold;
	  margin-bottom: 0;
	}

	.position {
	  margin: 0;
	}

	.bio {
	  margin: 10px;
	  padding: 10px;
	  border-top: 2px solid #00000077;
	  text-align: left;
	}

	p {
	  margin: 0 0 10px 0;
	}
  }
}
